<template>
  <input
    :type="type"
    :value="value"
    class="input"
    :class="className"
    @input="$emit('input', $event.target.value)"
    :placeholder="placeholder"
  >
</template>

<script>
export default {
  name: 'myInput',
  props: {
    type: String,
    value: {
      type: [Number, String],
      required: true,
    },
    className: {
      type: String,
    },
    placeholder: {
      type: String,
    },
  },
};
</script>

<style  lang="less" scoped>
  .input{
    width: 200px;
    height: 56px;
    outline: none;
    border: aquamarine solid 1px;
    font-size: 32px;
    color:#333;
  }
  .input:focus{
    border: chartreuse solid 1px;
  }
  .input::-webkit-input-placeholder {
    color: #999;
  }

</style>



