<template>
  <div id="app">
    <h1>{{user.user.name}}</h1>
    <button @click="save({name: '123'})">修改名称</button>
    <Input
      type="text"
      v-model="name"
      @input="save({name: name})"
      placeholder="请输入姓名"
      class="text-box"
    />
    <router-view/>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import Input from './common/input';

export default {
  name: 'App',
  data() {
    return {
      name: '',
    };
  },
  computed: {
    ...mapState({
      user: state => state.user,
    }),
  },
  mounted() {
    this.getData({ name: 123 });
  },
  methods: {
    ...mapActions('user', [
      'save', 'getData', 'translate']),
  },
  components: {
    Input,
  },
};
</script>

<style lang="less" scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.text-box {
  .input {
    border: none!important;
  }
}

</style>



