/* eslint-disable*/
import axios from 'axios';
import { getServer } from '../utils';


/* http
* 参数
* host：String Required 当前环境的域名端口，
* options：Obiect {
* closeTips：Boolean关闭默认接口错误消息提示 默认开启
* closeDefaultErrorTips Boolean是否关掉默认提示，默认开启
* timeout：Number超时时间：默认30000
* dataType: String 默认为json
* }*/

export default class Http {

  constructor(host,api,options) {
    this.options = {
      closeTips: true,
      closeDefaultErrorTips: true,
      timeout: 30000,
      dataType: 'json',
      ...options,
    };
    this.get = this.get.bind(this);
    this.post = this.post.bind(this);
    this.host = getServer(host);
    this.request = this.request.bind(this);
    this.getAxios = this.getAxios.bind(this);
  }
  getAxios(options){
    const axiosImp = axios.create({
      headers: {'Content-Type':'application/json;charset=UTF-8'},
      ...options
    });
    return {
      axiosImp
    }
  }
  request({ type= 'post', api= '', data= {}, options= {} }){
    const { axiosImp } = this.getAxios({
      ...this.options,
      ...options
    });
    return new Promise((resolve, reject) => {
      axiosImp[type](this.host + api, {  ...data  }, ).then(res => {
        if (res.data.status.toUpperCase() === 'ERROR') {
          if(this.options.closeTips) {
            reject(res);
            return window.alert(res.data.content)
          } else {
            resolve(res.data.content);
          }
        } else {
          return resolve(res.data.content)
        }

      }, () => {
        if(this.options.closeDefaultErrorTips) {
          return window.alert('加载异常，请稍后再试')
        }
      })
    });
  }
  get(api = '', data = {}, options = {}) {
    return this.request({ type: 'get', api, data, options: { ...this.options, ...options } })

  }
  post(api = '', data = {}, options = {}) {
    return this.request({ type: 'post', api, data, options: { ...this.options, ...options } })
  }
}



