import { getData } from '../../services/user';


const initState = {
  user: { name: 'mahui', age: 22 },
  checkoutStatus: null,
};

// getters
const Getters = {
  getName: state => (
    state.user.name
  ),
};
const actions = {
  save({ commit }, payload) {
    commit('changeName', payload);
  },
  getData({ commit }, payload) {
    getData(payload).then((res) => {
      commit('setUser', res);
    });
  },
};
const mutations = {
  changeName(state, { name }) {
    state.user.name = name;
  },
  setUser(state, payload) {
    state.user = { ...payload };
  },
};

export default {
  namespaced: true,
  state: initState,
  getters: Getters,
  actions,
  mutations,
};




